import React, {Component} from "react";
import Header from "./components/Header";
import logoWork from "./images/Matte Work Logo.svg"
import Worklis from "./components/work/Worklis";
import HeaderWork from "./components/work/HeaderWork";
import back from "./images/Background1.jpg"
import background from "./images/MobileWorkBg2.jpg"
import WorkOne from "./components/work/WorkOne";


class Work extends Component{
    constructor() {
        super();
        this.state = {
            width: window.innerWidth,
        };
    }
    componentWillMount() {
        window.addEventListener('resize', this.handleWindowSizeChange);
    }

// make sure to remove the listener
// when the component is not mounted anymore
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleWindowSizeChange);
    }

    handleWindowSizeChange = () => {
        this.setState({ width: window.innerWidth });
    };
    render() {
        const { width } = this.state;
        const isMobile = width <= 500;
            if (isMobile){
                return (
                    <div style={{background:`url(${background})`, backgroundPosition:'center top',height:'100vh'}}>
                        <HeaderWork logo={logoWork} hover='white' link={3}/>
                        <WorkOne></WorkOne>
                    </div>
                );
            }
            else {
                return(<div style={{background:`url(${back})`, backgroundPosition:'center'}}>
                        <Header logo={logoWork} hover='white' link={3}/>
                        <Worklis/>
                    </div>
                );
            }

    }

}
export default Work;