import React, {Component} from "react";
import {Link} from "react-router-dom";
import logo from "../../images/Matte Work Logo.svg"
import styled from "styled-components";

class Header extends Component{

    state={
        Hamburger:'',
        siteNav:'',

    }


    expand =()=>{
        this.setState((current)=>({
            Hamburger: current.Hamburger !== 'open' ? 'open': '',
            siteNav: current.siteNav !== 'show' ? 'show': ''
        }))
    }

    render() {
        const List = styled.li`
          a:hover {
                color: ${this.props.hover};
                cursor: pointer;
                }
    `
        const ListContainer = styled.ul`
            li:nth-child(${this.props.link}) a{
                    color:white;
                    border-bottom: 3px solid white;
            }
            background: ${this.props.hover==='white'? 'black': 'white'};
        `
        return(
            <div className="container fix" style={{background:'black'}}>
            <div className="row" style={{background:'transparent'}}>
                <div className="col-12 d-flex flex-wrap  align-items-center fix" style={{background:'transparent'}}>
                    <div className="site-branding d-flex align-items-center">
                        <Link className="d-block" to="/" rel="home"><img className="d-block" src={logo}
                                                                         style={{height: 40, width: 100}}
                                                                         alt="logo"/> </Link>
                    </div>
                    <nav className={`site-navigation d-flex align-items-center ${this.state.siteNav}`}>
                        <ListContainer className="d-flex flex-column flex-lg-row  align-items-center">
                            <List><Link to="/">HOME</Link></List>
                            <List><Link to="/story">OUR STORY</Link></List>
                            <List><Link to="/work">WORK</Link></List>
                            <List className="leftBarContact"><Link to="/contact" style={{fontWeight: 300}}>CONTACT</Link>
                            </List>
                        </ListContainer>
                    </nav>
                    <div className={`hamburger-menu d-lg-none ${this.state.Hamburger}`} onClick={this.expand} >
                        <span style={{background:'white'}}></span>
                        <span style={{background:'white'}}></span>
                        <span style={{background:'white'}}></span>
                        <span style={{background:'white'}}></span>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

export default Header;