import React,{Component} from "react";
import logo from "../../images/Matte Logo.png"
import whiteLogo from "../../images/Matte Logo White.png"
import {Link} from "react-router-dom";
import styled from "styled-components";
import {CSSTransition, SwitchTransition, TransitionGroup} from "react-transition-group";

class ShowDesktop extends Component{
    constructor() {
        super();
        this.state = {
            width: window.innerWidth,
        };
    }
    componentWillMount() {
        window.addEventListener('resize', this.handleWindowSizeChange);
    }

// make sure to remove the listener
// when the component is not mounted anymore
    componentWillUnmount() {
        window.removeEventListener('resize', this.handleWindowSizeChange);
    }

    handleWindowSizeChange = () => {
        this.setState({ width: window.innerWidth });
    };
    render() {
        const {property,change,linkList} = this.props
        const { width } = this.state;
        const isMobile = width <= 500;
        const Next= styled.li`
            text-align:right;
            padding-right: 9px;
            :hover{
                color:${property.theme};
                cursor: pointer;
            }
            
        `
        const Cont=styled.div`
            background:url(${typeof property.mobileI === 'undefined'? property.image: isMobile ? property.mobileI : property.image}) center center;
            background-size: cover;
            background-repeat:no-repeat;
            height:100vh
         `
        return(<Cont>
                <div className="container">
                    <TransitionGroup>
                        <CSSTransition
                            timeout={1000}
                            classNames="slide"
                            appear
                        >
                            <div className="header-works">
                                <div>
                                    <img src={property.theme === 'white' ? whiteLogo : logo} className="shoWork"
                                         alt="logo"/>
                                </div>
                                <div className="showBackWork">
                                    <Link to="/work" className="back" style={{background:`url(${property.back_link})`,backgroundPosition:'center',
                                        backgroundRepeat: 'no-repeat',
                                        backgroundSize: `${isMobile ? '25px' :'40px'}`}}>close</Link>
                                </div>
                            </div>
                        </CSSTransition>
                    </TransitionGroup>
                    <TransitionGroup>
                        <CSSTransition
                            timeout={500}
                            classNames="item"
                            appear
                        >
                            <div className="list-works">
                                <ul>
                                    {linkList.map((element)=>(<Next className={property.id===element && (property.theme === 'black' ? 'active-black': 'active-white')} onClick={()=>change(element)}>{element}</Next>))}
                                </ul>
                            </div>
                        </CSSTransition>
                    </TransitionGroup>


                    <TransitionGroup>
                        <CSSTransition
                            timeout={500}
                            classNames="item"
                            appear
                        >
                            <div className="view-works">
                                <Link to={`/work/${property.id}`}>
                                    <img src={property.view} alt="design matte" height={150} width={190} />
                                </Link>
                            </div>
                        </CSSTransition>
                    </TransitionGroup>
                </div>
            </Cont>
        )
    }
}
export default ShowDesktop