import React,{Component} from "react";
import ShowDesktop from "./ShowDesktop";
import topher from "../../images/Topher-BG.jpg";
import codekids from "../../images/9jacodekids.jpg";
import spex from "../../images/Spex-Shots.jpg";
import kasone from "../../images/kasone.jpg";
import white from "../../images/Back-arrow-white.svg"
import black from "../../images/Back-arrow.svg"
import blackView from "../../images/View Project Black.svg"
import whiteView from "../../images/View Project White.svg"
import mob_9jacode from "../../images/Mobile9jacodeBG.jpg"
import mob_kasone from "../../images/MobilekasBG.jpg"
import mob_Topher from "../../images/MobileTopher.jpg"

const data = [
    {
        id: 'Topher',
        image: topher,
        mobileI:mob_Topher,
        back_link: white,
        view:whiteView,
        link:'/work/topher',
        theme:'white'
    },
    {
        id: '9jacodekids',
        image: codekids,
        mobileI:mob_9jacode,
        back_link: black,
        view:blackView,
        link:'/work/9jacodekids',
        theme:'black'
    },
    {
        id: 'Kasone',
        image: kasone,
        mobileI:mob_kasone,
        back_link: white,
        view:whiteView,
        link:'/work/kasone',
        theme:'white'
    },
    {
        id: 'Spex',
        image: spex,
        back_link: black,
        view:blackView,
        link:'/work/spex',
        theme:'black'
    }
]


class Identity extends Component{
    state={
        data:data[0]
    }
    onChange =(n)=>{
        console.log(n)
        switch (n) {
            case 'Topher': this.setState(()=>({
                data:data[0]
            }));
            break;
            case '9jacodekids': console.log(n) ;this.setState(()=>({
                data:data[1]
            }));
                break;
            case 'Kasone': this.setState(()=>({
                data:data[2]
            }));
                break;
            case 'Spex': this.setState(()=>({
                data:data[3]
            }));
                break;

        }
    }
    render() {
        return(
            <ShowDesktop property={this.state.data} change={this.onChange} linkList={['Topher','9jacodekids','Kasone','Spex']}/>
        )
    }

}
export default Identity