import React,{Component} from "react";
import ShowDesktop from "./ShowDesktop";
import autostar from "../../images/Autostar.jpg";
import whiteView from "../../images/View Project White.svg";
import choice from "../../images/9ja_s-choice.jpg";
import black from "../../images/Back-arrow.svg";
import blackView from "../../images/View Project Black.svg";
import nant from "../../images/10nants-BG.jpg";
import mob_9jachoice from "../../images/Mobile9JASCHOICEBG.jpg"
import mob_10nants from "../../images/Mobile10NANTSBG.jpg"
import mob_Autostar from "../../images/MobileAutoBG.jpg"
const data = [
    {
        id: '9jachoice',
        image: choice,
        mobileI:mob_9jachoice,
        back_link: black,
        view:blackView,
        link:'/work/9jachoice',
        theme:'black'
    },
    {
        id: '10nants',
        image: nant,
        mobileI:mob_10nants,
        back_link: black,
        view:blackView,
        link:'/work/10nants',
        theme:'black'
    },
    {
        id: 'Autostar',
        image: autostar,
        mobileI:mob_Autostar,
        back_link: black,
        view:blackView,
        link:'/work/autostar',
        theme:'black'
    },
]
class Ui_ux extends Component{
    constructor() {
        super();
        this.state = {
            data: data[0]
        };
    }
    onChange =(n)=>{
        console.log(n)
        switch (n) {
            case '9jachoice': this.setState(()=>({
                data:data[0]
            }));
                break;
            case '10nants': console.log(n) ;this.setState(()=>({
                data:data[1]
            }));
                break;
            case 'Autostar': this.setState(()=>({
                data:data[2]
            }));
                break;

        }
    }
    render() {
        return(
            <ShowDesktop property={this.state.data} change={this.onChange} linkList={['9jachoice','10nants','Autostar']}/>
        )
    }

}
export default Ui_ux