import React,{Component} from "react";
import identity from "../../images/Identity WORK.png"
import Ui from "../../images/UiUx WORK.png"
import Web from "../../images/Web WORK.png"
import Dare from "../../images/dare to be different.svg"
import ReactTouchEvents from "react-touch-events";
import {CSSTransition, SwitchTransition} from "react-transition-group"
import WorkOneComp from "./WorkOneComp";

const data = [
    {
        id: 'no1',
        image: identity,
        altImage: 'Best Identity branding',
        text:'Identity Design',
        link:'/work/identity'
    },
    {
        id: 'no2',
        image: Ui,
        altImage: 'Web Interaction and interfaces',
        text:'Ui/Ux Design',
        link:'/work/ui-ux'
    },
    {
        id: 'no3',
        image: Web,
        altImage: 'Web development at the best',
        text:'Web Development',
        link:'/work/web'
    }
]

class WorkOne extends Component{
    state={
        comp:1,
        data:data[0]
    }
    handleScroll = (direction)=>{
        if (direction === 'top') {
            this.state.comp >= 3 ? this.setState((current) => ({
                    comp: 1,
                    data: data[0]
                })) :
                this.setState((current) => ({
                    comp: current.comp + 1,
                    data:data[current.comp]
                }))
        }else {
            this.state.comp <= 1 ? this.setState((current) => ({
                    comp: 3,
                    data:data[2]
                })) :
                this.setState((current) => ({
                    comp: current.comp - 1,
                    data:data[current.comp-2]
                }))
        }
    }
    handleClick=(n)=>{
        this.setState(()=>({
            comp: n,
            data:data[n-1]
        }))
    }
    render(){
        return(
            <div>
            <div className="container">
                <ReactTouchEvents  onSwipe={(direction)=>this.handleScroll(direction)}>
                    <div>
                        <div className="row"  style={{paddingTop:'170px',overflow:'hidden'}} >
                            <SwitchTransition>
                                <CSSTransition
                                    key={this.state.data.id}
                                    timeout={500}
                                    classNames="item"
                                    appear
                                >
                                    <WorkOneComp component={this.state.data} />
                                </CSSTransition>
                            </SwitchTransition>
                        </div>
                <div className="carousel-indicators d-flex flex-column">
                    <li onTouchStart={()=>this.handleClick(1)} onClick={()=>this.handleClick(1)}  className={this.state.comp ===1 && 'active'}></li>
                    <li onTouchStart={()=>this.handleClick(2)} onClick={()=>this.handleClick(2)}  className={this.state.comp ===2 && 'active'}></li>
                    <li onTouchStart={()=>this.handleClick(3)} onClick={()=>this.handleClick(3)}  className={this.state.comp ===3 && 'active'}></li>
                </div>
                    </div>
                </ReactTouchEvents>
            </div>


            <img src={Dare} style={{ marginTop:'10%'}}/>

        </div>)
    }
}
export default WorkOne