import React,{Component} from "react";
import ShowDesktop from "./ShowDesktop";
import nfh from "../../images/Niger-Hospital.jpg";
import uhere from "../../images/Uhere.jpg";
import white from "../../images/Back-arrow-white.svg"
import whiteView from "../../images/View Project White.svg"

const data = [
    {
        id: 'NFH',
        image: nfh,
        back_link: white,
        view:whiteView,
        link:'/work/nfh',
        theme:'white'
    },
    {
        id: 'Uhere',
        image: uhere,
        back_link: white,
        view:whiteView,
        link:'/work/Uhere',
        theme:'white'
    }
]


class Web extends Component{
    state={
        data:data[0]
    }
    onChange =(n)=>{
        console.log(n)
        switch (n) {
            case 'NFH': this.setState(()=>({
                data:data[0]
            }));
                break;
            case 'Uhere': console.log(n) ;this.setState(()=>({
                data:data[1]
            }));
                break;

        }
    }
    render() {
        return(
            <ShowDesktop property={this.state.data} change={this.onChange} linkList={['NFH','Uhere']}/>
        )
    }

}
export default Web